html, body {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  font-family: sans-serif;
  background: #fff;
  color: #4d148c;
}

table {
  display: flex;
  flex: 1;
  width: 100%;
  text-align: center;
  color: black;
}

table tr:first-child td {
  background: black;
  color: #fff;
}

tr td {
  padding: 10px;
  border: 1px black solid;
}

@media print {
  @page
   {
    size: 11in 8.5in;
    size: landscape;
  }
  tr td {
  padding: 10px;
  border: 1px black solid;
  }
}